/// Estilos para la secci贸n de publicidad.
///
/// Esta secci贸n est谩 dise帽ada para mostrar anuncios de manera destacada, con un fondo llamativo y un dise帽o con una animaci贸n sutil en el fondo. Los elementos dentro de la secci贸n incluyen un eslogan, un icono representativo y un bot贸n de llamada a la acci贸n.
/// La secci贸n tiene un fondo de color principal con un patr贸n diagonal de l铆neas semitransparentes.
