@use "../base/mixins" as *;
@use "../base/variables" as *;

/// Estilos para el pie de p谩gina (Footer).
///
/// El pie de p谩gina tiene un fondo oscuro con texto blanco. Los elementos est谩n distribuidos en un
/// dise帽o flexible que se adapta a diferentes tama帽os de pantalla. Tambi茅n incluye una secci贸n de
/// redes sociales y una de suscripci贸n a un bolet铆n.
///
/// @group Footer Section
.footer {
  background: darken($secondary-color, 5%); // Fondo ligeramente m谩s oscuro que el color secundario
  color: white; // Color de texto blanco
  padding: 40px 0; // Espaciado en la parte superior e inferior
  text-align: center;

  /// Estilo para el contenido del pie de p谩gina.
  ///
  /// La secci贸n del contenido se distribuye en una fila flexible. El contenido es responsivo,
  /// adapt谩ndose a pantallas m谩s peque帽as.
  .footer__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    max-width: 1200px;
    margin: 0 auto; // Centra el contenido
    padding: 20px;

    @media (max-width: 768px) {
      flex-direction: column; // Cambia la direcci贸n a columna en pantallas peque帽as
      text-align: center; // Centra el texto
    }
  }

  /// Estilo para la informaci贸n general.
  ///
  /// Se incluye una secci贸n con texto centrado que puede contener informaci贸n del sitio o
  /// derechos de autor.
  .footer__info {
    flex: 1; // Toma todo el espacio disponible
    text-align: center;

    .footer__text {
      font-size: 0.9rem; // Tama帽o de fuente m谩s peque帽o
      margin-bottom: 10px;
      color: rgba(255, 255, 255, 0.8); // Color blanco con opacidad para el texto
    }
  }

  /// Estilos para los iconos de redes sociales.
  ///
  /// Los iconos son enlaces que cambian de color y tama帽o al pasar el rat贸n por encima.
  .social-icons {
    margin-top: 10px;

    a {
      color: white; // Color blanco por defecto
      margin: 0 12px; // Espaciado entre los iconos
      font-size: 1.6rem; // Tama帽o del icono
      transition: transform 0.3s ease, color 0.3s ease; // Transici贸n para el cambio de color y tama帽o

      ///  Efecto hover sobre los iconos de redes sociales.
      ///
      /// Al pasar el rat贸n por encima, el color cambia a un tono m谩s claro y el icono aumenta de tama帽o.
      &:hover {
        color: lighten($primary-color, 10%); // Cambia el color a un tono m谩s claro
        transform: scale(1.1); // Aumenta el tama帽o
      }

      /// Estilo cuando un icono est谩 en foco.
      ///
      /// Cuando el icono es enfocado (por ejemplo, cuando es seleccionado con el teclado),
      /// se agrega un borde alrededor de 茅l.
      &:focus {
        outline: 2px solid $primary-color; // Resalta con un borde
        border-radius: 5px; // Bordes redondeados para el enfoque
      }
    }
  }

  /// Estilos para el formulario de suscripci贸n al bolet铆n.
  ///
  /// La secci贸n del bolet铆n contiene un formulario para que los usuarios se suscriban al
  /// bolet铆n. Los campos de entrada tienen un estilo distintivo y el bot贸n tiene una animaci贸n
