    ///
    /// El formulario se ajusta para ser responsivo y tiene un dise帽o flexible.
    form {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      gap: 10px;
    }

    /// Estilo para el campo de entrada de correo electr贸nico.
    ///
    /// El campo de entrada tiene un borde redondeado y cambia de color y sombra cuando es enfocado.
    input[type="email"] {
      padding: 12px 15px; // Espaciado dentro del campo
      border: 2px solid transparent;
      border-radius: 25px;
      outline: none;
      transition: border-color 0.3s ease, box-shadow 0.3s ease; // Transici贸n suave para borde y sombra
      width: 260px;
      font-size: 1rem;
      background: rgba(255, 255, 255, 0.1); // Fondo semitransparente
      color: white;

      &::placeholder {
        color: rgba(255, 255, 255, 0.6); // Color del texto del placeholder
      }

      /// Estilo cuando el campo de correo electr贸nico est谩 enfocado.
      ///
      /// Se cambia el color del borde y se agrega una sombra alrededor del campo.
      &:focus {
        border-color: $primary-color;
        box-shadow: 0 0 10px rgba($primary-color, 0.5); // Sombra alrededor
        background: rgba(255, 255, 255, 0.2); // Fondo m谩s oscuro cuando se enfoca
      }
    }

    /// Estilo para el bot贸n de env铆o del bolet铆n.
    ///
    /// El bot贸n de env铆o tiene un estilo distintivo y efectos de hover para hacerlo interactivo.
    button {
