      padding: 12px 20px;
      border-radius: 25px;
      font-size: 1rem;
      transition: background 0.3s ease, transform 0.2s ease-in-out;

      /// Efecto hover sobre el bot贸n.
      ///
      /// Al pasar el rat贸n sobre el bot贸n, cambia el color de fondo y el tama帽o del bot贸n.
      &:hover {
        background: lighten($primary-color, 10%); // Cambio de color de fondo
        transform: scale(1.05); // Aumento de tama帽o
      }
    }
  }
}

/// **Media Queries** para mejor responsividad.
///
/// Asegura que el pie de p谩gina se ajuste adecuadamente en pantallas m谩s peque帽as,
/// especialmente las redes sociales y la secci贸n de bolet铆n.
@media (max-width: 768px) {
  .footer__content {
    flex-direction: column; // Cambia la direcci贸n a columna en pantallas peque帽as
    text-align: center;
  }

  .newsletter form {
    flex-direction: column; // Organiza los elementos en una columna
    gap: 15px; // Espaciado entre los campos
  }

  .social-icons {
    margin-top: 15px;
  }
}
